<script>
import PhotoSwipe from "photoswipe/dist/photoswipe.js";
import PhotoSwipeUI_Default from "photoswipe/dist/photoswipe-ui-default.js";
import "photoswipe/dist/photoswipe.css";
import "photoswipe/dist/default-skin/default-skin.css";
import { init_swiper } from '@/assets/js/swiper.js'
export default {
  data() {
    return {
      SWITCH: false,
      distanceY: 0,
      targetNode: null,
      pageIndex: 0,
      swiperObj: null
    };
  },
  template: "#class_circle",
  mounted() {
    var that = this
    this.swiperObj = init_swiper(this.$refs.swiper_wrapper, document.querySelectorAll('.swiper-slide').length, window.innerWidth, function(index) {
      that.pageIndex = index;
    })
    var v_box = document.getElementById("v_box");
    var v_load = document.getElementById("v_load");
    var v_bg = document.getElementById("v_bg");
    var video_wrap = document.getElementById("video_wrap");
    // initSwiper('#circle_wrap', '#circle_nav .tab', 1);
    this.initPhotoSwipeFromDOM(".my-gallery"); // 初始化
    var startY;
    // 事件监听
    v_box.addEventListener("transitionend", () => {
      if (!this.SWITCH) {
        video_wrap.style.display = "none";
        v_box.pause();
        this.swiitch_trsition(false);
      } else {
        v_box.play();
      }
    });
    v_box.addEventListener("canplay", () => {
      v_load.style.display = "none";
    });
    v_box.addEventListener("playing", () => {
      v_load.style.display = "none";
    });

    v_box.addEventListener("touchstart", e => {
      var touch = e.targetTouches[0];
      startY = touch.pageY;
      this.swiitch_trsition(false);
    });
    v_box.addEventListener("touchmove", e => {
      var touch = e.targetTouches[0];
      var endY = touch.pageY;
      this.distanceY = endY - startY;
      e.preventDefault();
      v_box.style["-webkit-transform"] = "translateY(" + this.distanceY + "px)";
      v_bg.style.opacity = 1 - Math.abs(this.distanceY) / 280;
    });

    v_box.addEventListener("touchend", () => {
      this.swiitch_trsition(true);
      v_box.style["-webkit-transform"] = "translateY(0px)";
      if (Math.abs(this.distanceY) > 50) {
        this.close_video();
      } else {
        v_bg.style.opacity = 1;
      }
    });
  },
  methods: {
    open_news() {
      this.$router.push('circle/newsRemind')
    },
    goPage(index) {
      this.swiperObj.goPage(index)
    },
    openVideo(event) {
      // 设置初始化
      var v_box = document.getElementById("v_box");
      var v_load = document.getElementById("v_load");
      var v_bg = document.getElementById("v_bg");
      var video_wrap = document.getElementById("video_wrap");

      var that = event.target;
      this.targetNode = event.target;
      v_load.style.display = "block";
      this.SWITCH = true;
      var scale =
        window.innerWidth /
        that.getBoundingClientRect().width *
        that.getBoundingClientRect().height;
      var top = (window.innerHeight - scale) / 2; // 计算合适top值
      video_wrap.style.display = "block";
      v_box.poster = that.src;
      v_box.src = that.dataset.video;
      v_box.style.width = that.getBoundingClientRect().width + "px";
      v_box.style.top = that.getBoundingClientRect().top + "px";
      v_box.style.left = that.getBoundingClientRect().left + "px";
      console.log(that.getBoundingClientRect().top);
      console.log(that.getBoundingClientRect().left);
      // 执行动画
      requestAnimationFrame(() => {
        this.swiitch_trsition(true);
        v_bg.style.opacity = 1;
        v_box.style.top = top + "px";
        v_box.style.left = 0 + "px";
        v_box.style.width = innerWidth + "px";
      });
    },
    swiitch_trsition(key) {
      if (key) {
        v_box.style.transition = "all 333ms cubic-bezier(0.4, 0, 0.22, 1)";
        v_bg.style.transition = "all 333ms cubic-bezier(0.4, 0, 0.22, 1)";
      } else {
        v_box.style.transition = "";
        v_bg.style.transition = "";
      }
    },
    close_video() {
      this.SWITCH = false;
      v_bg.style.opacity = 0;
      v_box.style.top = this.targetNode.getBoundingClientRect().top + "px";
      v_box.style.left = this.targetNode.getBoundingClientRect().left + "px";
      v_box.style.width = this.targetNode.getBoundingClientRect().width + "px";
    },
    initPhotoSwipeFromDOM(gallerySelector) {
      // 解析来自DOM元素幻灯片数据（URL，标题，大小...）
      // (children of gallerySelector)
      var parseThumbnailElements = function(el) {
        var thumbElements = el.childNodes,
          numNodes = thumbElements.length,
          items = [],
          figureEl,
          linkEl,
          size,
          item,
          divEl;

        for (var i = 0; i < numNodes; i++) {
          figureEl = thumbElements[i]; // <figure> element

          // 仅包括元素节点
          if (figureEl.nodeType !== 1) {
            continue;
          }
          divEl = figureEl.children[0];
          linkEl = divEl.children[0]; // <a> element

          size = linkEl.getAttribute("data-size").split("x");

          // 创建幻灯片对象
          item = {
            src: linkEl.children[0].getAttribute("src"),
            w: parseInt(size[0], 10),
            h: parseInt(size[1], 10)
          };

          if (figureEl.children.length > 1) {
            // <figcaption> content
            item.title = figureEl.children[1].innerHTML;
          }

          if (linkEl.children.length > 0) {
            // <img> 缩略图节点, 检索缩略图网址
            item.msrc = linkEl.children[0].getAttribute("src");
          }

          item.el = figureEl; // 保存链接元素 for getThumbBoundsFn
          items.push(item);
        }
        console.log(items);
        return items;
      };

      // 查找最近的父节点
      var closest = function closest(el, fn) {
        return el && (fn(el) ? el : closest(el.parentNode, fn));
      };

      // 当用户点击缩略图触发
      var onThumbnailsClick = function(e) {
        e = e || window.event;
        e.preventDefault ? e.preventDefault() : (e.returnValue = false);

        var eTarget = e.target || e.srcElement;

        // find root element of slide
        var clickedListItem = closest(eTarget, function(el) {
          return el.tagName && el.tagName.toUpperCase() === "FIGURE";
        });

        if (!clickedListItem) {
          return;
        }

        // find index of clicked item by looping through all child nodes
        // alternatively, you may define index via data- attribute
        var clickedGallery = clickedListItem.parentNode,
          childNodes = clickedListItem.parentNode.childNodes,
          numChildNodes = childNodes.length,
          nodeIndex = 0,
          index;

        for (var i = 0; i < numChildNodes; i++) {
          if (childNodes[i].nodeType !== 1) {
            continue;
          }

          if (childNodes[i] === clickedListItem) {
            index = nodeIndex;
            break;
          }
          nodeIndex++;
        }

        if (index >= 0) {
          // open PhotoSwipe if valid index found
          openPhotoSwipe(index, clickedGallery);
        }
        return false;
      };

      // parse picture index and gallery index from URL (#&pid=1&gid=2)
      var photoswipeParseHash = function() {
        var hash = window.location.hash.substring(1),
          params = {};

        if (hash.length < 5) {
          return params;
        }

        var vars = hash.split("&");
        for (var i = 0; i < vars.length; i++) {
          if (!vars[i]) {
            continue;
          }
          var pair = vars[i].split("=");
          if (pair.length < 2) {
            continue;
          }
          params[pair[0]] = pair[1];
        }

        if (params.gid) {
          params.gid = parseInt(params.gid, 10);
        }

        return params;
      };

      var openPhotoSwipe = function(
        index,
        galleryElement,
        disableAnimation,
        fromURL
      ) {
        var pswpElement = document.querySelectorAll(".pswp")[0],
          gallery,
          options,
          items;

        items = parseThumbnailElements(galleryElement);

        // 这里可以定义参数
        options = {
          barsSize: {
            top: 100,
            bottom: 100
          },
          fullscreenEl: false,
          shareButtons: [
            { id: "wechat", label: "分享微信", url: "#" },
            { id: "weibo", label: "新浪微博", url: "#" },
            {
              id: "download",
              label: "保存图片",
              url: "{{raw_image_url}}",
              download: true
            }
          ],

          // define gallery index (for URL)
          galleryUID: galleryElement.getAttribute("data-pswp-uid"),

          getThumbBoundsFn: function(index) {
            // See Options -> getThumbBoundsFn section of documentation for more info
            var thumbnail = items[index].el.getElementsByTagName("img")[0], // find thumbnail
              pageYScroll =
                window.pageYOffset || document.documentElement.scrollTop,
              rect = thumbnail.getBoundingClientRect();

            return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };
          }
        };

        // PhotoSwipe opened from URL
        if (fromURL) {
          if (options.galleryPIDs) {
            // parse real index when custom PIDs are used
            for (var j = 0; j < items.length; j++) {
              if (items[j].pid == index) {
                options.index = j;
                break;
              }
            }
          } else {
            // in URL indexes start from 1
            options.index = parseInt(index, 10) - 1;
          }
        } else {
          options.index = parseInt(index, 10);
        }

        // exit if index not found
        if (isNaN(options.index)) {
          return;
        }

        if (disableAnimation) {
          options.showAnimationDuration = 0;
        }

        // Pass data to PhotoSwipe and initialize it
        console.log(pswpElement);
        gallery = new PhotoSwipe(
          pswpElement,
          PhotoSwipeUI_Default,
          items,
          options
        );

        gallery.init();
      };

      // loop through all gallery elements and bind events
      var galleryElements = document.querySelectorAll(gallerySelector);
      for (var i = 0, l = galleryElements.length; i < l; i++) {
        galleryElements[i].setAttribute("data-pswp-uid", i + 1);
        galleryElements[i].onclick = onThumbnailsClick;
      }

      // Parse URL and open gallery if it contains #&pid=3&gid=1
      var hashData = photoswipeParseHash();
      if (hashData.pid && hashData.gid) {
        openPhotoSwipe(
          hashData.pid,
          galleryElements[hashData.gid - 1],
          true,
          true
        );
      }
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/style/fn.scss";

.swiper-wrapper {
  width: 300%;
  display: flex;
  align-items: start;
}
.swiper-slide {
  flex: 1;
}
.circle {
 
}
.circle_wrap {
  margin-bottom: 10px;
  margin-top: 34px;
  overflow: hidden;
}
.circle .circle_nav {
  position: fixed;
  top: 50px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  color: #999;
  font-size: 13px;
  line-height: 1.5;
  border-bottom: 3px solid #f8f8f8;
  background: #fff;
  z-index: 1;
}
.circle .circle_nav .tab.active {
  padding-bottom: 10px;
  border-bottom: 2px solid #fcbd43;
  color: #fcbd43;
}
.circle .circle_main {
}
.circle .lists {
  padding: 0 10px;
  margin-top: 16px;
  padding-bottom: 12px;
}
.circle .lists .msg {
  display: inline-block;
}
.circle .lists .lists_top {
}
.circle .lists .lists_left {
  display: inline-block;
  max-width: 27px;
  margin-right: 9px;
  margin-top: 4px;
}
.circle .lists .lists_left img {
  max-width: 100%;
}
.circle .lists .lists_right {
}
.circle .lists .msg .name {
  font-size: 13px;
  color: #666;
}
.circle .lists .msg .class_icon {
  max-height: 12px;
  vertical-align: middle;
}
.circle .lists .msg .time {
  font-size: 10px;
  color: #999;
}
.circle .lists .operate {
  margin-top: 6px;
}
.circle .lists .follow {
  display: inline-block;
  margin-right: 10px;
  background: url(~@/assets/image/fw.png) no-repeat;
  width: e3(177px);
  height: e3(81px);
  background-size: cover;
  vertical-align: middle;
}
.circle .lists .cricle-more {
  max-width: 16px;
  vertical-align: middle;
}
.lists > .info {
}
.lists .text {
  margin-bottom: 6px;
}
.lists .class_info {
  color: #1155cc;
}
.lists .my-gallery img {
  max-width: 98px;
}
.lists .my-gallery .big_img img {
  max-width: e3(1025px);
  max-height: e3(414px);
  vertical-align: top;
}
.lists .class_title {
  padding: 4px 10px 4px 23px;
  background: #eee url(~@/assets/image/class_icon.png) no-repeat 5px center;
  background-size: 15px;
  border-radius: 15px;
  color: #1082ff;
  font-size: 10px;
}
.lists .interact {
  margin-top: 10px;
  display: flex;
}
.lists .interact span {
  height: 15px;
  flex: 1;
  color: #666;
}
.lists .interact span img {
  max-height: 100%;
}

.ad-title {
}
.ad-img {
  padding: 5px 0;
  width: 100%;
}
.ad-explain {
  font-size: 10px;
  color: #c8c8c8;
}
.ad-info {
  margin: 8px 0;
}
.category {
  background: #fcbd43;
  color: #fff;
  font-size: e3(32px);
  border-radius: 10px;
  padding: 0 5px;
}
.viewing {
  width: e3(32px);
  height: e3(24px);
  margin-left: e3(14px);
}
.vn {
  font-size: e3(28px);
  color: #c8c8c8;
}
.video-img {
  position: relative;
  display: inline-block;
}
.video-img::before {
  display: block;
  content: "";
  background: url(~@/assets/image/video-icon.png) no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 40px;
  height: 40px;
}
.video-wrap {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  z-index: 1000;
  display: none;
}
.v-bg {
  background: #000;
  width: 100%;
  height: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
}
.v-box {
  position: absolute;
}
/* loader */
.v-loader {
  position: relative;
  margin: 0 auto;
  width: 50px;
  height: 50px;
  top: 50%;
  margin-top: -25px;
  z-index: 100;
  display: none;
}
.loader:before {
  content: "";
  display: block;
  padding-top: 100%;
}
.circular {
  animation: rotate 2s linear infinite;
  height: 100%;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}

@keyframes color {
  100%,
  0% {
    stroke: #d62d20;
  }
  40% {
    stroke: #0057e7;
  }
  66% {
    stroke: #008744;
  }
  80%,
  90% {
    stroke: #ffa700;
  }
}
</style>




