<template>
  <div class="regist base-page">
    <back title="注册"></back>
    <div class="login-form border-top-1px">
      <div class="login-box">
        <mobile-inp @change="mobile_inp_change">
        </mobile-inp>
        <validate-inp @change="code_change" :mobileNb="mobileNb">
        </validate-inp>
        <psw-inp @change="change_psw">
        </psw-inp>
      </div>
      <sub-btn content="注册" @trigger="sub_login"></sub-btn>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import mobileInp from "@/base/mobileInp.vue";
import validateInp from "@/base/validateInp.vue";
import pswInp from "@/base/pswInp.vue";
import subBtn from '@/base/subBtn.vue'
import { checkPhone, validate_psw, get_validate } from "@/assets/js/util.js";
import back from "@/base/back.vue";

var send_status = [
  '注册成功',
  '用户已存在',
  '验证码错误',
  '注册失败',
  'openid已存在'
];

export default {
  components: {
    back,
    pswInp,
    mobileInp,
    validateInp,
    subBtn
  },
  data() {
    return {
      mobileNb: '',
      codeNb: '',
      pswNb: '',
      validateText: '获取验证码',
      disable: false
    };
  },
  methods: {
    code_change(v) {
      this.codeNb = v
    },
    mobile_inp_change(v) {
      this.mobileNb = v
    },
    change_psw(data) {
      this.pswNb = data;
    },
    back() {
      this.$router.back();
    },
    sub_login() {
      if (!checkPhone(this.mobileNb)) return;
      if (!validate_psw(this.pswNb)) return;
      axios
        .post(
          "http://192.168.110.14/xlp/api.php",
          "x=app/user_info/10002&username=" +
            this.mobileNb +
            "&password=" +
            this.pswNb +
            "&code=" +
            this.codeNb
        )
        .then(rp => {
          if (rp.data.status === "0") {
            alert("注册成功");
          } else {
            alert(send_status[rp.data.status]);
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.login-form {
  padding: 30px 15px 0;
}

.login-box {
  padding-bottom: 10px;
}

</style>



