<template>
    <div class="input-bar" :class="{active: active, user: type}">
        <input type="number" class="mobile-inp" placeholder="请输入手机号" v-model="mobileNb" 
        @focus="active = true"
        @blur="active = false"
        >
        <div class="clear-btn form-btn" @click="clear_inp"></div>
    </div>
</template>

<script>
export default {
  props: [
    'type'
  ],
  data() {
    return {
        mobileNb: '',
        active: false
    }
  },
  watch: {
    mobileNb: function (newData) {
      this.$emit('change', newData)
    }
  },
  methods: {
    clear_inp() {
      this.mobileNb = ''
    }
  }
};
</script>

<style scoped lang="scss">
.input-bar {
  position: relative;
  border-bottom: 1px solid #eee;
  background-size: auto 20px;
  background-repeat: no-repeat;
  background-position: 10px center;
  padding: 5px 0;
  padding-left: 35px;
  margin-bottom: 20px;
  background-image: url(~@/assets/image/Mobile_ic.png);
}
.user {
  background-image: url(~@/assets/image/user_ic.png);
}
.input-bar.active {
  border-color: #fcbd43;
}
.form-btn {
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  right: 10px;
  top: 10px;
}
.clear-btn {
  background-image: url(~@/assets/image/clear_ic.png);
  width: 10px;
  height: 10px;
}
</style>



