<template>
    <div class="base-page news">
        <back title="消息提醒">
        </back>
        <nav class="nav">
            <div class="nav-item active">回复</div>
            <div class="nav-item">赞</div>
            <div class="nav-item">系统</div>
        </nav>
        <div class="content">
            <div class="no-news">
                <img src="@/assets/image/no-news.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
import back from "@/base/back.vue";
export default {
  components: {
    back
  }
};
</script>

<style scoped lang="scss">
.news {
}
.nav {
  display: flex;
  justify-content: space-around;
  margin-bottom: 3px;
  background: #fff;
  position: fixed;
  width: 100%;
}
.nav-item {
  padding: 5px 0;
  font-size: 13px;
}
.nav-item.active {
  border-bottom: 2px solid #fcbd43;
  color: #fcbd43;
}
.content {
    height: 2000px;
    padding-top: 30px;
    position: relative;
}
.no-news {
    width: 100%;
    height: 200px;
    position: absolute;
    top: 100px;
    text-align: center;
}
.no-news img {
    width: 150px;
}
</style>



