import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home/home.vue'
import circle from '@/components/circle/circle.vue'
import dialog from '@/components/dialog/dialog.vue'
import me from '@/components/me/me.vue'
import match from '@/page/match.vue'
import chat from '@/base/chat.vue'
import article from '@/page/article.vue'
import loginPage from '@/page/loginPage.vue'
import login from '@/page/login.vue'
import regist from '@/page/regist.vue'
import voice from '@/page/voice.vue'
import stjl from '@/page/stjl.vue'
import medata from '@/page/medata.vue'
import maillist from '@/page/maillist.vue'
import integral from '@/page/integral.vue'
import comment from '@/page/comment.vue'
import report from '@/page/report.vue'
import ring from '@/page/ring.vue'
import order from '@/page/order.vue'
import msgReminding from '@/page/msgReminding.vue'
import activity from '@/page/activity.vue'
import live from '@/page/live.vue'
import myComment from '@/page/myComment.vue'
import myZan from '@/page/myZan.vue'
import myCircle from '@/page/myCircle.vue'
import forgetPsw from '@/page/forgetPsw'
import circleDetails from '@/page/circleDetails.vue'
import newAddFollow from '@/page/newAddFollow.vue'
import fans from '@/page/fans.vue'
import follow from '@/page/follow.vue'
import liveList from '@/page/liveList.vue'
import newsRemind from '@/page/newsRemind.vue'
import details from '@/page/details.vue'
import system from '@/page/system.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/loginPage',
      component: loginPage,
      children: [
        {
          path: 'login',
          component: login
        },
        {
          path: 'regist',
          component: regist
        },
        {
          path: 'forgetPsw',
          component: forgetPsw
        }
      ]
    },
    {
      path: '/chat',
      component: chat
    },
    { path: '/', redirect: 'loginPage' },
    {
      path: '/home',
      name: 'home',
      component: home,
      children: [
        {
          path: 'match',
          component: match,
        },
        {
          path: 'article/:id',
          component: article,
          children: [
            {
              path: 'comment',
              component: comment
            }
          ]
        },
        {
          path: 'voice',
          component: voice
        },
        {
          path: 'stjl',
          component: stjl
        }
      ]
    },
    {
      path: '/circle',
      name: 'circle',
      component: circle,
      children: [
        {
          path: 'circleDetails',
          component: circleDetails          
        },
        {
          path: 'newsRemind',
          component: newsRemind
        }
      ]
    },
    {
      path: '/dialog',
      name: 'dialog',
      component: dialog
    },
    {
      path: '/me',
      name: 'me',
      component: me,
      children: [
        {
          path: 'medata',
          component: medata
        },
        {
          path: 'maillist',
          component: maillist,
          children: [
            {
              path: 'details',
              component: details
            }
          ]
        },
        {
          path: 'system',
          component: system
        },
        {
          path: 'integral',
          component: integral
        },
        {
          path: 'report',
          component: report
        },
        {
          path: 'ring',
          component: ring
        },
        {
          path: 'order',
          component: order
        },
        {
          path: 'msgReminding',
          component: msgReminding
        },
        {
          path: 'myComment',
          component: myComment
        },
        {
          path: 'myZan',
          component: myZan
        },
        {
          path: 'myCircle',
          component: myCircle
        },
        {
          path: 'newAddFollow',
          component: newAddFollow
        },
        {
          path: 'fans',
          component: fans
        },
        {
          path: 'follow',
          component: follow
        }
      ]
    },
    {
      path: '/activity',
      component: activity
    },
    {
      path: '/live',
      component: live
    },
    {
      path: '/liveList',
      component: liveList
    }
  ]
})



