<template>
  <div id="app">
    <more @clearMore="clearMore" v-show="isShow"></more>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <el-footer @openMore="openMore"></el-footer>
  </div>
</template>

<script>
import elFooter from "@/components/footer/el-footer.vue"
import more from "@/components/more/more.vue"

export default {
  name: "App",
  components: {
    elFooter,
    more
  },
  data() {
    return {
      isShow: false
    };
  },
  methods: {
    openMore() {
      this.isShow = true;
    },
    clearMore() {
      this.isShow = false;
    }
  }
};
</script>

<style>
.more {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #fff;
}

.more_box {
  position: absolute;
  bottom: 50px;
  width: 100%;
}

.more-ul {
  display: flex;
  padding: 0 33px;
  flex-wrap: wrap;
}

.more-li {
  width: 25%;
  margin-bottom: 20px;
  text-align: center;
}

.more-li img {
  width: 50px;
}

.more-clear {
  position: relative;
}

.more-clear::after,
.more-clear::before {
  content: "";
  display: block;
  width: 20px;
  height: 5px;
  background: #a8a8a7;
  position: absolute;
  left: 50%;
  margin-left: -10px;
}

.more-clear::after {
  transform: rotate(45deg);
}

.more-clear::before {
  transform: rotate(-45deg);
}
</style>



