<template>
  <div class="login_index base-page">
    <router-view></router-view>
    <div class="logo">
      <img src="@/assets/image/logo.png" alt="">
    </div>
    <p class="msg">学令牌玩的好 读书考试没烦恼</p>
    <div class="login-form">
      <router-link to="/loginPage/login">
        <button class="login-btn" id="lg">登录</button>
      </router-link>
      <router-link to="/loginPage/regist">
        <button class="regist-btn">注册</button>
      </router-link>
    </div>
    <div class="wechat-login">
      <p class="wc-title">
        <i></i>微信快速注册登录
        <i></i>
      </p>
      <img src="@/assets/image/wx_ic.png" alt="微信登录" class="wx-ic" @click="wx_loging">
      <p>
        <span class="determine">我已阅读并同意《用户协议》及《用户协议》</span>
      </p>
    </div>
  </div>
</template>

<script>
var auth_status = [
  "登录成功",
  "用户取消",
  "用户拒绝授权",
  "当前设备未安装微信客户端"
];
var get_token_status = [
  "成功",
  "apikey值为空或非法",
  "apiSecret值为空或非法",
  "code值为空或非法",
  "网络超时"
];
var get_user_info_status = [
  "成功",
  "accessToken过期",
  "openId非法",
  "openId值为空",
  "accessToken值为空",
  "accessToken非法",
  "网络超时"
];
import axios from "axios";
import { serverUrl } from "@/assets/js/config.js";
export default {
  beforeCreate() {
    // alert(localStorage.getItem("uTk"));
    if (localStorage.getItem("uTk")) {
      var url = serverUrl + "?&x=app/news_info/10000&page=0&token=" + localStorage.getItem("uTk");
      axios
        .get(url)
        .then(rp => {
          if(rp.data.status === '0') {
            this.$router.push('/home')
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  data() {
    return {
      wx: "",
      code: "",
      accessToken: "",
      openId: ""
    };
  },
  methods: {
    wx_loging() {
      this.wx = api.require("wx");
      this.wx_auth();
    },
    wx_auth() {
      this.wx.auth(
        {
          apiKey: "wxdc86ebed0e740605"
        },
        (ret, err) => {
          if (ret.status) {
            this.code = ret.code;
            this.wx_get_token();
          } else {
            alert(auth_status[err.code]);
          }
        }
      );
    },
    wx_get_token() {
      this.wx.getToken(
        {
          code: this.code
        },
        (ret, err) => {
          if (ret.status) {
            this.accessToken = ret.accessToken;
            this.openId = ret.openId;
            this.wx_get_userInfo();
          } else {
            alert(get_token_status[err.code]);
          }
        }
      );
    },
    wx_get_userInfo() {
      this.wx.getUserInfo(
        {
          accessToken: this.accessToken,
          openId: this.openId
        },
        (ret, err) => {
          if (ret.status) {
            this.sub_send(this.openId, ret.nickname, ret.headimgurl);
          } else {
            alert(get_user_info_status[err.code]);
          }
        }
      );
    },
    sub_send(openId, nickName, headimgUrl) {
      axios
        .post(
          serverUrl,
          `?x=app/user_info/10000&openid=${openId}&nickname=${nickName}&headimgurl=${headimgUrl}`
        )
        .then(rp => {
          if (Number(rp.data.status) === 0) {
            localStorage.setItem("uTk", rp.data.token);
            alert("保存Token为: " + localStorage.getItem("uTk"));
          } else {
            alert(auth_status[rp.data.status]);
          }
        })
        .catch(error => {
          alert(error);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.login_index {
  background-image: url(~@/assets/image/login_bg.png);
  background-repeat: no-repeat;
  background-size: 100% 385px;
  padding-top: 0;
  background-color: #fff;
}

.logo {
  margin: 91.3333px auto 23.333px;
  border-radius: 5px;
  text-align: center;
}

.logo img {
  width: 151px;
}

.msg {
  font-size: 20px;
  color: #fff;
  text-align: center;
  margin-bottom: 47px;
}

.login-form {
  border-radius: 10px;
  background: #fff;
  padding: 40px 20px;
  margin: 0 auto;
  width: 266px;
  box-shadow: 0 12px 38px -5px #e8e4e4;
}

.login-btn,
.regist-btn {
  width: 100%;
  text-align: center;
  font-size: 20px;
  border-radius: 25px;
  padding: 0;
  border: 1px solid #fcbd43;
  line-height: 39px;
}

.login-btn {
  background: #fcbd43;
  color: #fff;
  margin-bottom: 30px;
}

.regist-btn {
  background: #fff;
  color: #fcbd43;
}

.wechat-login {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
}

.wc-title {
  margin-bottom: 18px;
}

.wc-title i {
  width: 50px;
  height: 1px;
  display: inline-block;
  background: #ccc;
  margin: 0 10px;
  margin-bottom: 3px;
}

.wechat-login p {
  text-align: center;
  font-size: 11px;
  color: #999;
}

.determine {
  display: inline-block;
  background: url(~@/assets/image/determine_ic.png) no-repeat;
  background-size: 15px;
  padding-left: 20px;
  background-position: 0 center;
  margin-bottom: 20px;
}

.wx-ic {
  width: 48px;
  margin-bottom: 23px;
}
</style>



