<template>
    <header class="common-header">
        <i class="back fl" @click="back"></i>
        <span class="common-title" v-if="title !== undefined">{{ title }}</span>
        <slot></slot>
    </header>
</template>

<script>
export default {
    props: ['title'],
    methods: {
        back() {
            this.$router.back()
        }
    }
};
</script>

<style lang="scss" scoped>
.scan {
  position: absolute;
  right: 15px;
  top: 18px;
  background: url(~@/assets/image/scan-ic.png) no-repeat;
  background-size: cover;
  width: 15px;
  height: 15px;
}
</style>



