<script>
import { init_swiper } from '@/assets/js/swiper.js'

export default {
  data() {
    return {
      chat_mode: null,
      emoji_index: 1,
      isEntry: false,
      isCursor: false
    };
  },
  methods: {
    back() {
      this.$router.go(-1)
    },
    start_entry() {
        this.isEntry = true;
    },
    end_entry() {
        this.isEntry = false;
    },
    open_voice() {
      this.chat_mode = "voice";
    },
    open_emoji() {
      this.chat_mode = "emoji";
      this.isCursor = true;
    },
    clear_chat_mode() {
      this.chat_mode = null;
      this.isCursor = false
    },
    add_emoji(event) {
      if (event.target.classList.contains("emoji")) {
        var span = document.createElement("span");
        span.className = event.target.classList.value;
        span.setAttribute("contenteditable", "false");
        this.$refs.inp_data.appendChild(span);
      }
    },
    init_swipper() {
      var target = this.$refs.emoji_wrapper;
      var that = this;
      init_swiper(target, document.querySelectorAll('.emoji-list').length,window.innerWidth, function(currentItem){
        that.emoji_index = currentItem + 1
      })
    },
    send() {
      return;
      var data = this.$refs.inp_data.innerHTML
      if (data == '') return;
      var div = document.createElement('div')
      var impUrl = require('@/assets/image/Header_navlink.png')
      div.className = 'release clearflx'
      div.innerHTML = `
                      <img class="me-pt fr" src="${impUrl}" alt="我的头像">
                      <span class="release-content fr">${data}</span>
                      `
      this.$refs.chat_log.appendChild(div)
      this.$refs.inp_data.innerHTML = ''
      this.$refs.char_wrap.scrollTop = 99999
    }
  },
  activated() {
    this.init_swipper()
    this.$refs.char_wrap.scrollTop = 99999
  }
};
</script>

<style lang="scss" scoped>
.common-more i {
