  animation: voice-play2 1s linear 0s infinite;
}
.play-bar3 {
  width: 10px;
  height: 10px;
  margin-left: -12px;
}


@keyframes voice-play1 {
    0% {
        border-left-color: transparent;
    }
    26% {
        border-left-color: transparent;
    }
    62% {
        border-left-color: #fff;
    }
    78% {
        border-left-color: #fff;
    }
    100% {
        border-left-color: transparent;
    }
}
@keyframes voice-play2 {
    0% {
        border-left-color: transparent;
    }
    26% {
        border-left-color: #fff;
    }
    78% {
        border-left-color: #fff;
    }
    100% {
        border-left-color: transparent;
    }
}
.voice-wrap.p {
  transform: rotateY(180deg);
  float: left;
}
.voice-wrap.p i {
  border-left-color: #bdbdbd;
}
.chat-tool {
  width: 100%;
  position: fixed;
  bottom: 0;
  text-align: center;
  background: #fff;
}
.chat-from {
  display: flex;
  line-height: 40px;
  padding: 5px 10px;
  align-items: center;
}
.chat-tool img {
  width: 30px;
  padding: 0 4px;
}
.inp-data {
  width: 200px;
  border: 1px solid #eee;
  border-radius: 7px;
  flex: 1;
  padding-left: 10px;
  text-align: left;
  line-height: 30px;
  word-break: break-all;
}
.inp-data.active:after {
    content: '';
    display: inline-block;
    width:1px;
    height:25px;
    background: #000;
    vertical-align: middle;
    animation: flashing 1s infinite;
}
@keyframes flashing {
  0% {
    opacity: 00;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
.inp-data:focus {
  outline: none;
}
.send {
  background: #fcbd43;
  color: #fff;
  padding: 5px 12px;
  border-radius: 5px;
  margin-left: 5px;
}
.voice-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1001;
  display: none;
}
.voice-bg {
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.4;
}
.voice-box {
  width: 100%;
  height: 280px;
}
.an::before,
.an::after {
  content: "";
  width: 100%;
  height: 100%;
  background: #fcbd43;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  z-index: -1;
}

@keyframes slow-big {
  0% {
    transform: scale(1);
    opacity: 0.7;
  }
  100% {
    transform: scale(1.7);
    opacity: 0;
  }
}

.an::before {
  animation: slow-big 1.2s infinite linear;
  -webkit-animation-delay: 0.6s;
}


.an::after {
  animation: slow-big 1.2s infinite linear;
}

.emoji-box {
  width: 100%;
  overflow: hidden;
}
.emoji-wrapper {
  width: 500%;
  display: flex;
}
.emoji-wrapper.state-slide {
  transition: all 0.3s;
}
.emoji-list {
  width: 20%;
  height: 260px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-around;
}
.emoji-row {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.inp-data /deep/ .emoji, .emoji,.emoji-clear {
