  width: 5px;
  height: 5px;
  background: #ccc;
  border-radius: 50%;
  margin: 0 5px;
}
.emoji-bar i.active {
  background: #333;
}
@for $i from 1 through 100 {
  .inp-data /deep/ .e#{$i}, .e#{$i} {
    background-position: -26px * ($i - 1);
  }
}
.voice {
  width: 82px;
  height: 82px;
  border-radius: 50%;
  background-color: #fcbd43;
  margin: 0 auto;
  text-align: center;
  padding: 12px;
  margin-bottom: 40px;
  margin-top: 100px;
  position: relative;
}
.voice .m-ic {
  height: 100%;
  width: auto;
}
.voice-prompt {
  text-align: center;
  font-size: 14px;
  color: #666;
}
.voice-cancel {
  width: 121px;
  border-radius: 10px;
  position: absolute;
  top: 0;
  margin-top: 180px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  padding: 10px;
}
.cancel-bg {
  background: #000;
  opacity: 0.4;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 6px;
  z-index: -1;
}
.cancel-ic {
  height: 50px;
  margin-bottom: 18px;
  margin-top: 10px;
}
.cancel {
  background: #b92e2b;
  color: #fff;
  font-size: 14px;
  border-radius: 3px;
}
</style>



