<template>
  <section class="me">
    <router-view></router-view>
    <header class="page-head">
      <span class="title">我的</span>
      <img src="@/assets/image/q-foin.png" alt="" @click="open_system">
    </header>
    <div class="me_info">
      <div class="info_top">
        <span class="name">汪舒丹</span>
        <img src="@/assets/image/auth.png" alt="" class="badge">
      </div>
      <p class="lw">这个人有点懒，什么都没留下...</p>
      <i class="me_photo" @click="open_set">
        <img src="@/assets/image/Header_navlink.png" alt="头像" width='100%'>
      </i>
    </div>
    <ul class="me-msg">
      <li>
        <router-link to="/me/integral">
          <p class="me-msg-vl">200</p>
          <p class="me-msg-key">积分</p>
        </router-link>
      </li>
      <li>
        <router-link to="/me/myCircle">
          <p class="me-msg-vl">10</p>
          <p class="me-msg-key">圈子</p>
        </router-link>
      </li>
      <li>
        <router-link to="/me/follow">
          <p class="me-msg-vl">20</p>
          <p class="me-msg-key">关注</p>
        </router-link>
      </li>
      <li>
        <router-link to="/me/fans">
          <p class="me-msg-vl">70</p>
          <p class="me-msg-key">粉丝</p>
        </router-link>
      </li>
    </ul>
    <nav class="me_nav">
      <router-link to="/me/maillist">
        <img src="@/assets/image/me-ic1.png" alt="好友">
        <p>我的好友</p>
      </router-link>
      <router-link to="/me/myCircle">
        <img src="@/assets/image/me-ic2.png" alt="圈子">
        <p>我的圈子</p>
      </router-link>

      <router-link to="/me/myComment">
        <img src="@/assets/image/me-ic3.png" alt="评论">
        <p>我的评论</p>
      </router-link>
      <router-link to="/me/myZan">
        <img src="@/assets/image/me-ic4.png" alt="赞">
        <p>我的赞</p>
      </router-link>
    </nav>
    <div class="me_list">
      <div class="list border-bottom-1px" @click="open_report">
        <span class="title">成绩单</span>
      </div>
      <div class="list border-bottom-1px" @click="open_ring">
        <span class="title">我的手环</span>
      </div>
      <div class="list border-bottom-1px" @click="open_order">
        <span class="title">商城订单</span>
      </div>
      <div class="list border-bottom-1px" @click="open_integral">
        <span class="title">积分明细</span>
      </div>
      <div class="list border-bottom-1px" @click="open_set">
        <span class="title">设置</span>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  methods: {
    open_set() {
      this.$router.push("/me/medata");
    },
    open_report() {
      this.$router.push("/me/report");
    },
    open_ring() {
      this.$router.push("/me/ring");
    },
    open_order() {
      this.$router.push("/me/order");
    },
    open_integral() {
      this.$router.push("/me/integral");
    },
    open_system() {
      this.$router.push('/me/system')
    }
  }
};
</script>

<style lang="scss" scoped>
.me > .me_info {
  margin: 0 16px;
  position: relative;
  margin-bottom: 25px;
}

.me > .me_info .info_top {
  padding: 8px 0;
}

.me > .me_info .name {
  color: #333;
  font-size: 27px;
}

.me > .me_info .level {
  color: #888;
  font-size: 12px;
}

.badge {
  width: 16px;
}

.lw {
  font-size: 9.33333px;
  color: #999;
}

.me > .me_info .info_nav .credit {
  line-height: 1.2;
  font-size: 13px;
  padding: 0 7px;
  border-radius: 15px;
  background: #ffc107;
  color: #fff;
}

.me > .me_info .me_photo {
  width: 72px;
  position: absolute;
  right: 0;
  top: 0;
}

.me-msg {
  display: flex;
  text-align: center;
  font-size: 10.66667px;
  padding: 0 19px;
  border-bottom: 3px solid #f8f8f8;
  border-top: 3px solid #f8f8f8;
}

.me-msg li {
  flex: 1;
  padding: 7.66667px 0;
}

.me-msg-vl {
  color: #666;
}

.me-msg-key {
  color: #999;
}

.me .me_nav {
  display: flex;
  padding: 19px 19px 10.33333px 19px;
  border-bottom: 3px solid #f8f8f8;
}

.me .me_nav a {
  flex: 1;
  text-align: center;
}

.me > .me_nav > a > img {
  height: 20px;
  margin-bottom: 10px;
}

.me > .me_nav > a > p {
  color: #9a9a9a;
  font-size: 12px;
}

.me > .me_list {
  padding: 0 19px;
}

.me > .me_list > .list {
  padding: 19.6666px 33.333px;
  position: relative;
}

.me > .me_list > .list > .title {
  color: #333;
  font-size: 14px;
}

.me > .me_list > .list > .info {
  color: #b2b2b2;
  font-size: 12px;
}

.me > .me_list > .list::after {
  content: "";
  position: absolute;
  right: 0;
  width: 8px;
  height: 8px;
  border-top: 2px solid #ccc;
  border-right: 2px solid #ccc;
  transform: rotate(45deg);
  top: 21px;
}

.me > .me_list > .list:nth-child(1) {
  background: url(~@/assets/image/me-ic5.png) no-repeat;
  background-size: 19px;
  background-position: 0;
}

.me > .me_list > .list:nth-child(2) {
  background: url(~@/assets/image/me-ic9.png) no-repeat;
  background-size: 19px;
  background-position: 0;
}

.me > .me_list > .list:nth-child(3) {
  background: url(~@/assets/image/me-ic7.png) no-repeat;
  background-size: 19px;
  background-position: 0;
}

.me > .me_list > .list:nth-child(4) {
  background: url(~@/assets/image/me-ic6.png) no-repeat;
  background-size: 19px;
  background-position: 0;
}

.me > .me_list > .list:nth-child(5) {
  background: url(~@/assets/image/me-ic8.png) no-repeat;
  background-size: 19px;
  background-position: 0;
}
</style>



