<template>
    <footer class="common-footer border-top-1px">
      <ul class="clearflx">
        <li class="item">
          <router-link to="/home">
            <p class="title">首页</p>
          </router-link>
        </li>
        <li class="item">
          <router-link to="/circle">
            <p class="title">圈子</p>
          </router-link>
        </li>
        <li class="footerAddBtn item">
          <img src="@/assets/image/scan.png" alt="" @click="open_more">
        </li>

        <li class="item">
          <router-link to="/dialog">
            <p class="title">聊聊</p>
          </router-link>
        </li>
        <li class="item">
          <router-link to="/me">
            <p class="title">我的</p>
          </router-link>
        </li>
      </ul>
    </footer>
</template>

<script>
export default {
  methods: {
    open_more() {
      this.$emit('openMore', true);
    }
  }
};
</script>

<style scoped>
.common-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #f8f9fc;
  padding: 7px;
  z-index: 1;
}

footer .item {
  float: left;
  width: 20%;
  text-align: center;
  font-size: 12px;
}

.item a {
  color: #ccc;
  background-position: center 0;
  background-size: 18px;
  background-repeat: no-repeat;
}

footer .item:nth-child(1) > a {
  background-image: url(~@/assets/image/home.png);
}
footer .item:nth-child(2) > a {
  background-image: url(~@/assets/image/circle.png);
}
footer .item:nth-child(4) > a {
  background-image: url(~@/assets/image/class.png);
}
footer .item:nth-child(5) > a {
  background-image: url(~@/assets/image/mine.png);
}
.item .title {
  padding-top: 22px;
}

.item .router-link-exact-active {
  color: #333;
}
.item:nth-child(1) > a.router-link-exact-active {
  background-image: url(~@/assets/image/home1.png);
}
.item:nth-child(2) > a.router-link-exact-active {
  background-image: url(~@/assets/image/circle1.png);
}
.item:nth-child(4) > a.router-link-exact-active {
  background-image: url(~@/assets/image/class1.png);
}
.item:nth-child(5) > a.router-link-exact-active {
  background-image: url(~@/assets/image/mine1.png);
}
.footerAddBtn img {
  width: 45px;
  height: 38px;
  margin: 0;
}
</style>




