// 工具函数
import axios from 'axios'
import { serverUrl } from './config.js'
export function checkPhone(mobile_nb) {
    // 校验手机号
    if (!(/^1[34578]\d{9}$/.test(mobile_nb))) {
        alert("手机号码有误，请重填");
        return false;
    }
    return true;
}

export function validate_psw(psw) {
    // 校验密码
    if (!(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/.test(psw))) {
        alert("请输入合适的密码");
        return false;
    }
    return true;
}

export function get_validate(mobile_nb) {
    // 获取验证码
    axios.post(serverUrl, 'x=app/user_info/10001&username=' + mobile_nb)
        .then(function (rp) {
            console.log(rp.data);
            if (rp.data.status === '0') {
                alert('发送成功')
            } else {
                return false
            }
        })
        .catch(function (error) {
            console.log(error);
        });
}


