<template>
  <div class="input-bar" :class="{active: active}">
    <input type="text" class="mobile-inp" placeholder="请输入验证码" 
    v-model="code" 
    @focus="active = true" 
    @blur="active = false"
    >
    <button class="get-validate" @click="get_code" :class="{disable: disable}">{{ validateText }}</button>
  </div>
</template>

<script>
import { checkPhone, validate_psw, get_validate } from "@/assets/js/util.js";
export default {
  props: ["mobileNb"],
  data() {
    return {
      code: '',
      disable: false,
      validateText: '获取验证码',
      active: false
    };
  },
  methods: {
    get_code() {
      if (this.disable) return;
      var that = this;
      if (checkPhone(this.mobileNb)) {
        alert(this.mobileNb)
        get_validate(this.mobileNb);
        that.disable = true;
        var count = 60;
        this.validateText = '重新获取' + count;
        function loop() {
          count--;
          if (count === 0) {
            that.validateText = '获取验证码';
            that.disable = false;
          } else {
            that.validateText = `重新获取 (${count}s)`;
            setTimeout(loop, 1000);
          }
        }
        setTimeout(loop, 1000);
      }
    }
  },
  watch: {
    code(newData) {
      this.$emit('change', newData);
    }
  }
};
</script>

<style scoped lang='scss'>
.input-bar {
  position: relative;
  border-bottom: 1px solid #eee;
  background-size: auto 17px;
  background-repeat: no-repeat;
  padding: 5px 0;
  padding-left: 35px;
  margin-bottom: 20px;
  background-image: url(~@/assets/image/validate_ic.png);
  background-position: 5px center;
}
.input-bar.active {
  border-color: #fcbd43;
}
.get-validate {
  position: absolute;
  right: 0;
  bottom: 5px;
  width: 30%;
  color: #fcbd43;
  font-size: 13px;
  line-height: 32px;
  border-radius: 15px;
  border: 1px solid #fcbd43;
  background: #fff;
  padding: 0;
}
.get-validate.disable {
  color: #999;
  border-color: #999;
}
</style>



