<template>
    <div class="dialog">
        <header class="page-head">
            <img src="@/assets/image/me-ic.png" alt="" style="width: 23px">
        </header>
        <div class="chatListOut" id="chatSwiper">
            <div class="chatList chatListSign bgSizeCommom item border-bottom-1px" @click="open_chat">
                <div class="item-scroller">
                    <div class="content active">
                        <div class="chatLeft fl"><img src="@/assets/image/pic17.png" alt="" class="imgSize"></div>
                        <div class="chatLeft2 fl">
                            <span>李丽丽老师</span>
                            <span>[38条] 涂涂: [图片]</span>
                        </div>
                        <div class="chatRight fr">
                            <span>下午3:31</span>
                            <span>剩余连线时间:30分钟</span>
                        </div>
                    </div>
                    <div class="del">删除</div>
                </div>
            </div>
            <div class="chatList bgSizeCommom item border-bottom-1px">
                <div class="item-scroller">
                    <div class="content">
                        <div class="chatLeft fl">
                            <span class="newSignCommon newSign">6</span><img src="@/assets/image/pic17.png" alt="" class="imgSize"></div>
                        <div class="chatLeft2 fl">
                            <span>李丽丽老师</span>
                            <span>[38条] 涂涂: [图片]</span>
                        </div>
                        <div class="chatRight fr">
                            <span>下午3:31</span>
                        </div>
                    </div>
                    <div class="del">删除</div>
                </div>
            </div>
            <div class="chatList bgSizeCommom item border-bottom-1px">
                <div class="item-scroller">
                    <div class="content">
                        <div class="chatLeft fl">
                            <span class="newSignCommon newSignMore">99</span><img src="@/assets/image/pic17.png" alt="" class="imgSize"></div>
                        <div class="chatLeft2 fl">
                            <span>李丽丽老师</span>
                            <span>[38条] 涂涂: [图片]</span>
                        </div>
                        <div class="chatRight fr">
                            <span>下午3:31</span>
                        </div>
                    </div>
                    <div class="del">删除</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    methods: {
        open_chat() {
            this.$router.push('/chat')
        }
    }
};
</script>

<style lang="scss" scoped>
.dialog_header {position:fixed;top: 0;width:100%;height: 50px;text-align: center;display: flex;align-items: center;justify-content: flex-end;padding: 0 16px;}

.chatListOut {overflow: hidden}

.spriteMan {width: 23px;height: 23px;}

.chatList {width: 100%;background: white;height: 69px;}

.chatLeft {width: 48px;height: 48px;border-radius: 5px;margin-right: 11px;position: relative;}

.chatLeft2 span:nth-of-type(1) {color: #262626;font-size: 16px;display: block;}

.chatLeft2 span:nth-of-type(2) {color: #a9a9a9;font-size: 13px;padding-top: 6px;}

.chatRight span:nth-of-type(1) {color: #a9a9a9;font-size: 12px;display: block;text-align: right;}

.chatListSign .chatRight span:nth-of-type(1) {color: white;}

.chatRight span:nth-of-type(2) {color: #656565;font-size: 9px;text-align: right;padding-top: 12px;}

.newSignCommon {background: #e1483c;color: white;text-align: center;font-size: 12px;position: absolute;}

.newSign {width: 17px;height: 17px;border-radius: 50%;line-height: 17px;right: -6px;top: -6px;}

.newSignMore {padding: 1px 4px;border-radius: 12px;right: -9px;top: -7px;}

.chatListOut .item {width: 100%;}

.chatListOut .item-scroller {width: 130%;/* 添加absolute属性消除安卓机上滑动删除按钮时的文字重影 */position: absolute;}

.chatListOut .content {width: 77%;height: 100%;float: left;box-sizing: border-box;padding: 10px 9px;}

.chatListOut .content.active {background: url("~@/assets/image/pic16.png") no-repeat;background-size: 100% 100%;}

.chatListOut .del {width: 23%;height: 100%;float: right;background: red;color: #fff;text-align: center;line-height: 68px;}
</style>



