<template>
    <div class="psw input-bar" :class="{active: active}">
        <input :type="inpStatus" class="psw-inp" placeholder="请输入密码(6-20位字母、数字组合)" v-model="pswNb" @focus="active = true" @blur="active = false">
        <div class="eye-btn form-btn" @click="switchPsw" :class="{hide: showPsw}"></div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      showPsw: true,
      pswNb: "",
      inpStatus: "password",
      active: false
    };
  },
  methods: {
    switchPsw() {
      this.showPsw = !this.showPsw;
      this.inpStatus === "password"
        ? (this.inpStatus = "text")
        : (this.inpStatus = "password");
    }
  },
  watch: {
    pswNb(newData) {
      this.$emit("change", newData);
    }
  }
};
</script>

<style lang="scss" scoped>
.input-bar {
  border-bottom: 1px solid #eee;
  background-size: auto 20px;
  background-repeat: no-repeat;
  background-position: 10px center;
  padding: 5px 0;
  padding-left: 35px;
  margin-bottom: 20px;
  position: relative;
  background-image: url(~@/assets/image/psw_ic.png);
}
.input-bar.active {
    border-color: #fcbd43;
}
.psw-inp {
  width: 270px;
  border: none;
}
.form-btn {
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  right: 10px;
  top: 10px;
}

.eye-btn {
  background-image: url(~@/assets/image/eye_ic.png);
  width: 17px;
  height: 17px;
  background-size: 17px;
}
.eye-btn.hide {
  background-image: url(~@/assets/image/eye-hide.png);
}
</style>



