<script>
import axios from "axios";
import Qs from "qs";
import { serverUrl } from "@/assets/js/config.js";
export default {
  data() {
    return {
      lists: [], // 渲染列表
      ad: [], // 广告
      news: [], // 新闻
      page: 0,
      canRq: true,
      scrollTop: 0,
      loadHtml: `<img src="${require("@/assets/image/loading.gif")}"/>`
    };
  },
  mounted: function() {
    // this.loadArticle(this.page);
  },
  activated() {
    var that = this;
    document.documentElement.scrollTop = that.scrollTop;
    document.body.scrollTop = that.scrollTop;
    // document.body.scrollTop = that.scrollTop
    // 在当前组件下开启滚动监听加载数据
    window.onscroll = function() {
      var sHeight =
        document.documentElement.scrollTop || document.body.scrollTop; //滚动高度
      that.scrollTop = sHeight;
      var wHeight = document.documentElement.clientHeight; //window
      var dHeight = document.documentElement.offsetHeight; //整个文档高度
      if (
        dHeight - (sHeight + wHeight) < 24 &&
        that.canRq === true &&
        that.$route.path === "/home"
      ) {
        that.loadArticle(++that.page);
      }
    };
  },
  deactivated() {
    // 不在当前组件下关闭监听
    window.onscroll = null;
  },
  methods: {
    handleImg(img) {
      return img.split(",");
    },
    handleTime(time) {
      time = parseInt(time);
      var long = +new Date() - 1527594375084;
      if (long / 1000 / 60 / 60 / 24 > 1) {
        return (
          new Date(time).getMonth() + 1 + "月" + new Date(time).getDate() + "号"
        );
      } else if (long / 1000 / 60 / 60 > 1) {
        return parseInt(long / 1000 / 60 / 60) + "小时前";
      } else if (long / 1000 / 60 > 1) {
        return parseInt(long / 1000 / 60) + "分钟前";
      } else {
        return "刚刚";
      }
    },
    loadArticle(page) {
      this.canRq = false;
      var data = {
        x: "app/news_info/10000",
        page: page
      };
      this.axios
        .get("", {
          params: data
        })
        .then(rp => {
          var data = rp.data.data;
          if (data.news.length === 0) {
            this.loadHtml = "没有更多了...";
            return;
          }
          var news = data.news;
          var ad = data.ad;
          var tmpl = []; // push模板
          var ad_surplus = 0;
          // 组织好模板
          for (var i = 0; i < news.length; i++) {
            if (i % 3 === 0 && i > 0 && ad[i / 3 - 1]) {
              ad_surplus++;
              ad[i / 3 - 1].ad = true; // 标记为广告
              tmpl.push(ad[i / 3 - 1]);
            }
            tmpl.push(news[i]);
          }
          // 渲染完新闻后是否还有未渲染的广告
          if (ad.length > ad_surplus) {
            tmpl = tmpl.concat(ad.splice(0, ad_surplus));
          }

          this.lists = this.lists.concat(tmpl);
          // 渲染延迟
          setTimeout(() => {
            this.canRq = true;
          }, 100);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    open_chat() {
      this.$router.push("/chat");
    },
    open_article(id) {
      this.$router.push("/home/article/" + id);
    },
    open_voice() {
      this.$router.push("/home/voice");
    },
    open_stjl() {
      this.$router.push("/home/stjl");
    }
  }
};
</script>

