<template>
    <div class="base-page">
        <back></back>
        <div class="bg"></div>
        <div class="text">
            <p class="msg" id="msg">按住按钮读出题目</p>
            <p class="tips">读完自动获得解答</p>
        </div>
        <div class="voice-wrap">
            <div class="voice" :class="{an: isEntry}" @touchstart="start_entry" @touchend="end_entry">
                <img src="@/assets/image/mai-ic.png" alt="麦">
            </div>

            <div class="waveWrapper waveAnimation">
            </div>
            <p class="handel-msg">按住说话</p>
        </div>
    </div>
</template>

<script>
import back from "@/base/back.vue";
export default {
  components: {
      back
  },
  data() {
    return {
      isEntry: false
    };
  },
  methods: {
    start_entry() {
      this.isEntry = true;
    },
    end_entry() {
      this.isEntry = false;
    },
    back() {
      this.$router.back();
    }
  }
};
</script>

<style scoped lang="scss">
.bg {
  background: url(~@/assets/image/viceo-bg.png) no-repeat;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-size: 100% 100%;
}

.text {
  margin-top: 30px;
  text-align: center;
}

.msg {
  color: #fcbd43;
  font-size: 17px;
  margin-bottom: 10px;
}

.tips {
  color: #f7cd7d;
  font-size: 13px;
}

.voice-wrap {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 125px;
  background: #fff;
}

.voice {
  width: 82px;
  height: 82px;
  border-radius: 50%;
  background: #fcbd43;
  padding: 12px;
  margin: 0 auto;
  text-align: center;
  margin-top: -55px;
  margin-bottom: 20px;
  position: relative;
}

.voice img {
  height: 100%;
}

.an::before,
.an::after {
  content: "";
  width: 100%;
  height: 100%;
  background: #fcbd43;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  z-index: -1;
}

@keyframes slow-big {
  0% {
    transform: scale(1);
    opacity: 0.7;
  }
  100% {
    transform: scale(1.7);
    opacity: 0;
  }
}

.an::before {
  animation: slow-big 1.2s infinite linear;
  -webkit-animation-delay: 0.6s;
}

.an::after {
  animation: slow-big 1.2s infinite linear;
}

.handel-msg {
  text-align: center;
  color: #f7cd7d;
}

.wave {
  z-index: -1;
  width: 400px;
  height: 400px;
  border-radius: 40%;
  background: #fff;
  position: absolute;
  top: -10px;
  background: #fff;
  animation: drift 1000ms infinite linear;
}

.one {
  opacity: 0.4;
}

.two {
  animation: drift 2000ms infinite linear;
  left: -180px;
  opacity: 0.4;
}

.three {
  animation: drift 3000ms infinite linear;
  left: -100px;
}

.four {
  animation: drift 7000ms infinite linear;
  left: -150px;
  opacity: 0.4;
}

.five {
  animation: drift 5000ms infinite linear;
  left: 50px;
}

.six {
  animation: drift 6000ms infinite linear;
  left: 100px;
  opacity: 0.4;
}

@keyframes drift {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  from {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

@keyframes move_wave {
  0% {
    transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    transform: translateX(-25%) translateZ(0) scaleY(0.55);
  }
  100% {
    transform: translateX(-50%) translateZ(0) scaleY(1);
  }
}

.waveWrapper {
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}

.waveWrapperInner {
  position: absolute;
  width: 100%;
  overflow: hidden;
  height: 100%;
  bottom: -1px;
  background-image: linear-gradient(to top, #86377b 20%, #27273c 80%);
}

.bgTop {
  z-index: 15;
  opacity: 0.5;
}

.bgMiddle {
  z-index: 10;
  opacity: 0.75;
}

.bgBottom {
  z-index: 5;
}

.wave {
  position: absolute;
  left: 0;
  width: 200%;
  height: 100%;
  background-repeat: repeat no-repeat;
  background-position: 0 bottom;
  transform-origin: center bottom;
}

.waveTop {
  background-size: 50% 100px;
}

.waveAnimation .waveTop {
  animation: move-wave 3s;
  -webkit-animation: move-wave 3s;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}

.waveMiddle {
  background-size: 50% 120px;
}

.waveAnimation .waveMiddle {
  animation: move_wave 10s linear infinite;
}

.waveBottom {
  background-size: 50% 100px;
}

.waveAnimation .waveBottom {
  animation: move_wave 15s linear infinite;
}
</style>



