export function init_swiper(node, items, itemWidth, fn) {
    var target = node;
    var startX, startY, distanceX, distanceY;
    var win_w = itemWidth || window.innerWidth;
    var currentPosition = 0;
    var backObj = {};
    var touchDirection;
    target.addEventListener("touchstart", e => {
        touchDirection = null
        distanceX = 0;
        var touch = e.targetTouches[0];
        startX = touch.pageX;
        startY = touch.pageY;
        target.style.transition = '';
    });

    target.addEventListener("touchmove", e => {
        var touch = e.targetTouches[0];
        var endX = touch.pageX;
        var endY = touch.pageY;
        distanceX = endX - startX;
        distanceY = endY - startY;

        if (!touchDirection) {
            if (Math.abs(distanceY) > Math.abs(distanceX)) {
                touchDirection = 'y'
            } else {
                touchDirection = 'x'
            }
        }
        if (touchDirection === 'x') {
            e.preventDefault()
        } else {
            return
        }
        // 如果它是第一项禁止往左划
        currentPosition === 0 && distanceX >= 0 ? (distanceX = 0) : "";
        // 如果它是最后一项禁止往右划
        currentPosition === items - 1 && distanceX <= 0 ? (distanceX = 0) : "";
        target.style["-webkit-transform"] =
            "translateX(" + (distanceX + -(currentPosition * win_w)) + "px)";
    });

    target.addEventListener("touchend", () => {
        if (touchDirection === 'y') return;
        target.style.transition = 'all 0.3s'
        if (distanceX >= 50) {
            // 往左滑
            currentPosition -= 1;
        } else if (distanceX <= -50) {
            // 往右滑
            currentPosition += 1;
        }
        target.style["-webkit-transform"] =
            "translateX(" + -(currentPosition * win_w) + "px)";
        target.style.height = target.children[currentPosition].offsetHeight + 'px'
        fn(currentPosition)
    });
    backObj.goPage = function (index) {
        currentPosition = index;
        target.style["-webkit-transform"] =
            "translateX(" + -(currentPosition * win_w) + "px)";
        fn(currentPosition)
    }
    return backObj;
}


