<template>
    <div class="base-page msg">
        <back title="消息提醒"></back>
        <ul class="list">
            <li class="item border-bottom-1px">
                <img src="@/assets/image/msg-ic1.png" alt="回复" class="item-img">
                <span class="item-title">回复</span>
                <span class="item-tips">1</span>
                <i class="item-more"></i>
            </li>
            <li class="item border-bottom-1px">
                <img src="@/assets/image/msg-ic2.png" alt="回复" class="item-img">
                <span class="item-title">赞</span>
                <span class="item-tips">1</span>
                <i class="item-more"></i>
            </li>
            <li class="item border-bottom-1px">
                <img src="@/assets/image/msg-ic3.png" alt="回复" class="item-img">
                <span class="item-title">新增关注</span>
                <span class="item-tips">1</span>
                <i class="item-more"></i>
            </li>
            <li class="item border-bottom-1px">
                <img src="@/assets/image/msg-ic4.png" alt="回复" class="item-img">
                <span class="item-title">系统消息</span>
                <i class="item-more"></i>
            </li>
            <li class="item border-bottom-1px">
                <img src="@/assets/image/msg-ic5.png" alt="回复" class="item-img">
                <span class="item-title">精选活动</span>
                <i class="item-more"></i>
            </li>
        </ul>
    </div>
</template>

<script>
import back from "@/base/back.vue";
export default {
  components: {
    back
  }
};
</script>
<style scoped lang="scss">
.msg {
  background: #f8f8f8;
}
.item {
  background: #fff;
  padding: 10px 15px;
  display: flex;
  align-items: center;
}
.item-img {
  width: 35px;
  margin-right: 15px;
}
.item-title {
  flex: 1;
}
.item-tips {
  background: red;
  border-radius: 50%;
  padding: 0 7px;
  font-size: 12px;
  color: #fff;
  margin-right: 5px;
}
.item-more {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-top: 2px solid #ccc;
  border-right: 2px solid #ccc;
  transform: rotate(45deg);
}
</style>



