<template>
  <div class="base-page login">
    <back title="登录"></back>
    <div class="login-form border-top-1px">
      <div class="tab clearflx" :class="{r: login_mode === 'psw'}">
        <div class="tab-item fl" @click="switch_login_mode('msg')" :class="{active: login_mode === 'msg'}">手机快速登录</div>
        <div class="tab-item fr" @click="switch_login_mode('psw')" :class="{active: login_mode === 'psw'}">账号密码登录</div>
      </div>
      <div class="login-box">
        <div v-show="login_mode === 'msg'">
          <mobile-inp @change="mobile_msg_chang"></mobile-inp>
          <validate-inp 
          @change="code_change" 
          :mobileNb="mobileNb"
          >
          </validate-inp>
        </div>
        <div v-show="login_mode === 'psw'">
          <mobile-inp @change="mobile_msg_chang" type="user"></mobile-inp>
          <psw-inp @change="psw_change"></psw-inp>
        </div>
      </div>
      <sub-btn content="登录" @trigger="sub"></sub-btn>
      <p class="forget-psw" @click="forget_psw">忘记密码?</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import mobileInp from "@/base/mobileInp.vue";
import validateInp from "@/base/validateInp.vue";
import pswInp from '@/base/pswInp.vue'
import subBtn from '@/base/subBtn.vue'
import back from '@/base/back.vue'
import { checkPhone, validate_psw, get_validate } from "@/assets/js/util.js";
import { serverUrl } from '@/assets/js/config.js'
var send_status = [
  "登录成功",
  "登录失败",
  "账号不存在",
  "密码错误",
  "验证码失失效了",
  "验证码错误",
  "微信登录openid不存在"
];

export default {
  components: {
    mobileInp,
    validateInp,
    pswInp,
    subBtn,
    back
  },
  data() {
    return {
      login_mode: 'msg',
      userName: '',
      pswNb: '',
      code: '',
      mobileNb: ''
    };
  },
  methods: {
    psw_change(v) {
      this.pswNb = v
    },
    mobile_msg_chang(v) {
      this.mobileNb = v;
    },
    code_change(v) {
      this.code = v;
    },
    switch_login_mode(v) {
      this.login_mode = v;
    },
    sub() {
      if (this.login_mode === "psw") {
        if (!checkPhone(this.userName)) return;
        if (!validate_psw(this.pswNb)) return;
        this.psw_login();
      } else {
        this.msg_login();
      }
    },
    psw_login() {
      axios
        .post(
          serverUrl,
          "x=app/user_info/10000&username=" +
            this.userName +
            "&password=" +
            this.pswNb
        )
        .then(rp => {
          console.log(typeof rp.data.status);
          if (Number(rp.data.status) === 0) {
            localStorage.removeItem("uTk");
            localStorage.setItem("uTk", rp.data.token);
            this.$router.push("/home");
          } else {
            alert(send_status[rp.data.status]);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    msg_login() {
      if (!checkPhone(this.mobileNb)) return;
      if (this.code.length === 0) {
        alert('请输入验证码')
        return;
      }
      axios
        .post(
          serverUrl,
          "x=app/user_info/10000&username=" +
            this.mobileNb +
            "&code=" +
            this.code
        )
        .then(rp => {
          console.log(typeof rp.data.status);
          if (Number(rp.data.status) === 0) {
            localStorage.setItem("uTk", rp.data.token);
            this.$router.push("/home");
          } else {
            alert(send_status[rp.data.status]);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    forget_psw() {
      this.$router.push('/loginPage/forgetPsw')
    }
  }
};
</script>

<style lagn="scss" scoped>
.login {
  background: #fff;
}
.login-form {
  padding: 0 15px;
}
.tab {
  line-height: 40px;
  position: relative;
  margin-bottom: 35px;
}

.tab::before {
  content: "";
  position: absolute;
  width: 20%;
  background: #fcbd43;
  height: 1px;
  bottom: 1px;
  left: 15%;
}
.tab.r::before {
  left: 65%;
}
.tab-item {
  width: 50%;
  text-align: center;
  font-size: 15px;
  color: #666;
}

.tab-item.active {
  color: #fcbd43;
}

.forget-psw {
  text-align: center;
  font-size: 12px;
  color: #666;
}
</style>



