<template>
  <div class="article base-page">
    <back title="文章详情"></back>
    <div class="content-wrap border-bottom-1px">
      <h2 class="m-title">{{ content.title }}</h2>
      <div class="m-msg">
        <span class="m-origin">学令牌官方</span>
        <span class="m-time">{{ handleTime(content.updatetime) }}</span>
      </div>
      <div class="content">
        <p class="c-font" v-html="content.content">
        </p>
        <p class="read-num">2000 | 阅读</p>
      </div>
    </div>
    <div class="comment">
      <h3>精彩评论</h3>
      <ul>
        <li class="item border-bottom-1px" v-for="item in comment_list">
          <div class="pr">
            <span class="reply-btn" @click="open_comment">{{ item.comments }}</span>
            <span class="praise" @click="up_lick($event, item.id, item.likeup)" :class="{ active: item.thismylike === '0'}">{{ item.likeup }}</span>
          </div>
          <img src="@/assets/image/Header_navlink.png" alt="" class="head-pt">
          <div class="cm-msg">
            <div class="landlord-name">{{ item.nickname }}</div>
            <div class="time">2018-06-15 14:52</div>
            <div class="cm-content">{{ item.content }}</div>
            <div class="reply" v-if="item.children.length > 0">
              <div class="reply-item" v-for="reply in item.children">
                <span class="reply-name">{{ reply.nickname }}</span>
                <span v-if="reply.parentName">回复:
                  <span class="reply-name">{{ reply.parentName }}</span>
                </span>
                <span v-else>:</span>
                <span class="reply-content">{{ reply.content }}</span>
              </div>
              <!-- <div class="lookAll">查看
                <span class="reply-num">14</span>条评论 >
              </div> -->
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="input-wrap">
      <input type="text" class="input-btn" placeholder="说出你的想法" @keypress.enter="sub_comment" v-model="comment_content">
      <span class="comment-all">12</span>
      <span class="transmit">10</span>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from "axios";

import Qs from "qs";
import back from "@/base/back.vue";
import { serverUrl } from "@/assets/js/config.js";
export default {
  components: {
    back
  },
  data() {
    return {
      comment_list: [],
      data: "",
      content: {},
      comment_content: ""
    };
  },
  computed: {
    handelLike() {
      
    }
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    get_content() {
      // 获取内容
      var data = {
        x: 'app/news_info/10001',
        id: 23
      }
      this.axios
        .get('',{
          params: data
        })
        .then(rp => {
          this.content = rp.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    handleTime(time) {
      time = parseInt(time);
      var long = +new Date() - 1527594375084;
      if (long / 1000 / 60 / 60 / 24 > 1) {
        return (
          new Date(time).getMonth() + 1 + "月" + new Date(time).getDate() + "号"
        );
      } else if (long / 1000 / 60 / 60 > 1) {
        return parseInt(long / 1000 / 60 / 60) + "小时前";
      } else if (long / 1000 / 60 > 1) {
        return parseInt(long / 1000 / 60) + "分钟前";
      } else {
        return "刚刚";
      }
    },
    get_comment() {
      // 获取评论
      var data = {
        x: 'app/news_info/10003',
        id: this.$route.params.id
      }

      this.axios
        .get('', {
          params: data
        })
        .then(rp => {
          if (rp.data.status === "0") {
            var data = rp.data.data;
            var deep;
            function deepFor(d, parentName) {
              for (var i = 0; i < d.length; i++) {
                parentName ? (d[i].parentName = parentName) : "";
                deep.push(d[i]);
                if (d[i].child.length !== 0) {
                  deepFor(d[i].child, d[i].nickname);
                }
              }
              return deep;
            }
            for (var i = 0; i < data.length; i++) {
              deep = [];
              data[i].children = deepFor(data[i].child);
            }
            this.comment_list = data;
          }
        })
        .catch(function(err) {
          alert(1);
          console.log(err);
        });
    },
    sub_comment() {
      var data = {
        x: 'app/news_info/10004',
        id: this.$route.params.id,
        value: this.comment_content,
        fid: 0
      }
      this.axios
        .post('', data)
        .then(rp => {
          if (rp.data.status === "0") {
            this.comment_content = "";
            alert("已评论");
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    up_lick(e, id, likeup) {
      console.log(id)
      var targetNode = e.target
      var status = targetNode.classList.contains('active')
      var url = serverUrl;
      var data = {
        x: "app/news_info/10005",
        id: id
      };
      this.axios
        .post('', data)
        .then(rp => {
          if (rp.data.status === "0") {
            if(status === true) {
              targetNode.classList.remove('active')
              // alert('取消点赞')
              targetNode.textContent = Number(targetNode.textContent) - 1
            } else {
               targetNode.classList.add('active')
              //  alert('点赞成功')
                targetNode.textContent = Number(targetNode.textContent) + 1
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    open_comment() {
      this.$router.push(this.$route.path + "/comment");
    }
  },
  mounted() {
    this.get_content();
    this.get_comment();
  }
};
</script>

<style lang="scss">
.article {
  margin-bottom: 50px;
  background: #fff;
}
.content-wrap {
  padding: 0 15px;
}
.article h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.m-msg {
  font-size: 11px;
  color: #999;
  margin-bottom: 10px;
}

.c-pt {
  max-width: 100%;
}

.c-font {
  font-size: 12px;
  line-height: 2;
}
.c-font img {
  max-width: 100%;
}
.read-num {
  font-size: 12px;
  color: #e4e4e4;
  position: relative;
}

.comment {
}

.comment h3 {
  font-size: 14px;
  line-height: 50px;
  padding-left: 15px;
}

.comment .item {
  display: flex;
  padding: 0 15px;
  position: relative;
  padding-bottom: 10px;
  margin-bottom: 16px;
}

.head-pt {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}

.cm-msg {
  flex: 1;
}

.landlord-name {
  font-size: 12px;
  color: #666;
}

.time {
  color: #ccc;
  font-size: 9px;
  margin-bottom: 7px;
}

.cm-content {
  font-size: 14px;
}

.pr {
  position: absolute;
  right: 15px;
  top: 0;
  font-size: 10px;
  color: #ccc;
}

.reply-btn {
  background: url(~@/assets/image/sprite2.png) no-repeat;
  background-size: 15px;
  padding-left: 20px;
  background-position: 0 center;
  display: inline-block;
  height: 15px;
}

.praise {
  background: url(~@/assets/image/sprite3.png) no-repeat;
  background-size: 15px;
  padding-left: 20px;
  margin-left: 15px;
  background-position: 0 center;
  display: inline-block;
  height: 15px;
}

.praise.active {
  background-image: url(~@/assets/image/upvote1.svg);
  color: #fcbd43;
}

.reply {
  background: #f8f8f8;
  font-size: 13px;
  margin-top: 15px;
}
.reply-item {
  margin-bottom: 10px;
}
.reply-name {
  color: #fcbd43;
}
.lookAll {
  color: #fcbd43;
  font-size: 14px;
  margin-top: 5px;
}
.input-wrap {
  position: fixed;
  bottom: 0;
  display: flex;
  padding: 10px 15px;
  justify-content: center;
  width: 100%;
  align-items: center;
  background: #fff;
}
.input-btn {
  flex: 1;
  background: #f1f1f1;
  border-radius: 20px;
  padding: 10px 10px;
}
.comment-all,
.transmit {
  background-size: 15px;
  padding-left: 20px;
  margin-left: 10px;
  background-position: 0 center;
  background-repeat: no-repeat;
  color: #999;
}
.comment-all {
  background-image: url(~@/assets/image/sprite2.png);
}
.transmit {
  background-image: url(~@/assets/image/transmit-ic.png);
}
</style>



