<template>
    <div class="base-page ring">
        <back title="我的手环">
            <i class="scan"></i>
        </back>
        <ul class="list bfc">
            <li class="item">
                <img src="@/assets/image/Header_navlink.png" alt="" class="item-pt">
                <div class="item-text">
                    <p>汪舒彤</p>
                    <p class="num">编号: 000000</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import back from "@/base/back.vue";
export default {
    components: {
        back
    }
};
</script>

<style lang="scss" scoped>
.ring {
    background: #f8f8f8;
}
.list {
}
.item {
  padding: 10px 15px;
  margin-top: 7px;
  position: relative;
  background: #fff;
}
.item::after {
  content: "";
  display: block;
  position: absolute;
  right: 10px;
  top: 20px;
  width: 10px;
  height: 10px;
  border-top: 2px solid #ccc;
  border-right: 2px solid #ccc;
  transform: rotate(45deg);
  margin-top: 8px;
}
.item-pt {
  width: 47px;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 10px;
}
.item-text {
  display: inline-block;
  vertical-align: middle;
}
.num {
  font-size: 11px;
  color: #666;
}
</style>



