<template>
    <div class="send-btn" @click="send">{{ content }}</div>
</template>

<script>
export default {
    props: [
        'content'
    ],
    methods: {
        send() {
            this.$emit('trigger')
        }
    }
};
</script>

<style lang="scss" scoped>
.send-btn {
  width: 222px;
  line-height: 35px;
  margin: 0 auto;
  background: #fcbd43;
  border-radius: 25px;
  text-align: center;
  color: #fff;
  margin-bottom: 30px;
  box-shadow: 0 5px 5px #fbd997;
}
</style>



